import React from 'react'
import { IndexLink, Link } from 'react-router'
import './Footer.scss'

export const Footer = () => (
  <div className='footer'>
    <h1>footer</h1>
    <IndexLink to='/' activeClassName='route--active'>
      Home
    </IndexLink>
    {'  '}
    <Link to='/counter' activeClassName='route--active'>
      Counter
    </Link>
  </div>
)

export default Footer



